import React from 'react';
import { Carousel } from 'react-carousel-minimal';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {faFacebook,faTwitter,faInstagram} from "@fortawesome/free-brands-svg-icons";



//Array of images passed to the carousel object  
// Not working correctly will be fixed in future update 

function ServiCarousel() {
  const carouselTitle = "PHOTO GALLERY"; 
  const dataArry = [
    {
      image: "https://www.murrieta-plumber.com/wp-content/uploads/2018/06/pipes-wide-1200x720.jpg",
      
    },
    {
      image: "https://cf.ltkcdn.net/homeimprovement/images/orig/175158-693x693-plumbing.jpg",
      
    },
    {
      image: "https://www.mrrooter.com/images/blog/iStock-673897822.jpg",
   
    },
  {
      image:"https://rikb.com/wp-content/uploads/2018/09/Timeless-Traditional-Bathroom-Daltile-1030x687.jpg",

    },
    {
      image: "https://thumbs.dreamstime.com/b/modern-furnished-minimalist-bathroom-light-grey-interior-design-metro-style-tiles-228710145.jpg",
      
    },
    {
      image: "https://premieresuites.com/propertyphotos/full/15f2c4d0-ee5a-eb11-8117-000d3a6008fd_full.jpg",
 
    },
   
    {
      image: "https://homesoftherich.net/wp-content/uploads/2014/12/Screen-Shot-2014-12-12-at-9.04.25-PM.png",

    },
    {
        image: "https://loveincorporated.blob.core.windows.net/contentimages/gallery/e6ac7bc1-0739-4fdb-ba61-796cf3797457-39-Kay-&-Burton.jpg",
  
    },
  ];
  return (
    <div class="container-fluid"> 
     <spam className="CarouselTitle">{carouselTitle}</spam> 
    <div className="ServiCarousel">
          <Carousel                   //Carousel object that contains the images from the array as well formates the image width, hight and time the images are on screen.
           data={dataArry}             //also the array is passed to the carousel object so it can display images accordingly 
            time={1000}
            width="800px"
            height="480px" 
            automatic={true}
            dots={true}
            slideImageFit="cover"
            thumbnails={true}
            thumbnailWidth="100px"
          />        
    </div>
  </div>
  );
}


/*  a display of socal media links */
const SocailMedia = () =>{
  return(  <div class="container-fluid"> 
  
          <div className = "social-container">
    
               
              <a href="https://www.facebook.com" className="socialMedia">
              <FontAwesomeIcon icon={faFacebook} size="2x" />
              </a>
              <a href="https://www.twitter.com" className="socialMedia">
              <FontAwesomeIcon icon={faTwitter} size="2x" />
              </a>
              <a href="https://www.instagram.com"className="socialMedia">
              <FontAwesomeIcon icon={faInstagram} size="2x" />
              </a>
           
  </div>
  </div>); 
}


function Services() {
    return (<div>
          <ServiCarousel /> 
          <SocailMedia/>
            
    </div> );
}

export default Services;