import React from 'react';



/*The following is usage of a map to display the list,with an assosiated key and discription to display the list for the services offered. Addtionally more services can be add
or subtracted from the list as user preference */

const DiscriptionName = () =>{
           const serviceTitle = "SERVICES OFFERED";
    const serviceList = [ 
                          {discription:"Residential & Commercial",key: 1 },
                          {discription: "Drain Cleaning",key:2},
                          {discription: "Fixture Installations & Repairs",key:3 },
                          {discription: "Renovations",key:4 },
                          {discription: "Water heater installation",key:5 },
                          {discription: "Counter top installation",key:6}, ];
    return( <div class="container-fluid"> 
              <p className = "serviceTitle">{serviceTitle}</p>                 
              <ul className ="serviceList">
               {serviceList.map((service)=>{
                      return( 
                             <li className ="list" key={service.key}>{service.discription}</li>)
               })}</ul>
           </div>
    );
} 


function OfferedFile() {
    return ( <div>  
               <DiscriptionName/> 
                       
            </div> );
}

export default OfferedFile;