import React from 'react';
import emailjs from "emailjs-com";
import { Form, Input, TextArea, Button } from 'semantic-ui-react';
import 'semantic-ui-css/semantic.min.css';
import Swal from 'sweetalert2';




const Contact = () =>{
 // This is currently disabled for obvious reasons. I am able to enable it very quickley if need be                              
const SERVICE_ID = "";        
const TEMPLATE_ID = "";        // The service_ID, Template_ID, and User_ID, are provided from the EmailJS API      
const USER_ID = "" ;

    const contactTitle = <a className="contactTitle"  href="#ContactUs" name = "ContactUs">CONTACT US</a>;
    const handleSubmit = (e)=>{
       
     
       
       
         e.preventDefault();
        // when message sent to user email a success or error sign will appear on the form 
        emailjs.sendForm(SERVICE_ID,TEMPLATE_ID,e.target,USER_ID).then((result)=>{
                           console.log(result.text);
                           Swal.fire({
                               icon:"success",
                               title:"Message Sent"
                           })                           
        },(error)=>{ console.log(error.text);
            Swal.fire({
                 icon:"error",
                 title:"Ooops something went wrong message did not sent",
                 text: error.text
            })

        });
        e.target.reset() // once sent or not the form resets to original state 
    };
    return(  <div class="container-fluid"> 
              <h1>{contactTitle}</h1>
            <div className ="contactMain">
            <Form onSubmit ={handleSubmit}>
                 <Form.Field id="form-input-control-email"
                             control={Input}
                             label="Email"
                             name = "user_email"
                             placeholder ="Email..."
                             required
                             icon="mail"
                             iconPosition="left"
                 />
                 <Form.Field id="form-input-control-last-name"
                             control={Input}
                             label="Name"
                             name="user_Lname"
                             placeholder="Name…"
                             required
                             icon="user circle"
                             iconPosition="left"
                  />
                     <Form.Field id="form-input-control-last-name"
                             control={Input}
                             label="Phone Number"
                             name="user_phone"
                             placeholder="Phone Number…"
                             required
                             icon="user circle"
                             iconPosition="left"
                  />
                  <Form.Field id="form-textarea-control-opinion"
                              control={TextArea}
                              label="Message"
                              name="user_message"
                              placeholder="Message…"
                              required
                   />
                    <Button type="submit" color="black">Submit</Button>
                            
            </Form>

    </div>
    </div>
    );
}

const AddInfo = () =>{
     const emergencyTitle = "Emergency Service Available";
      const emergencySentance = <text>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Pellentesque ultricies volutpat ligula. <br/>Orci varius natoque penatibus et magnis dis 
           nascetur ridiculus mus.<br/> Quisque malesuada, orci nec luctus lacinia, quam leo feugiat est,
           ut porta purus est nec est. Nam lacinia elit ut viverra tincidunt.</text> 
    return( <div class="container-fluid"> 
                <p className="emergencyTitle">{emergencyTitle}</p> 
                <p className ="emergencySentance">{emergencySentance}</p>       
           </div>);
}

const ContactInfo = () =>{
            const areaOfServiceTitle = <text>Areas Served</text>
            const companyName = <text>Company Name</text>
            
            const areaOfService = [{discription: "Kitchener", key: 1},
                                   {discription: "Guelph"},
                                   {discription: "Cambridge"},
                                   {discription: "Hamilton"},
                                   {discription: "Fergus"}, 
                                   ];
            const CompanyOfService = [{discription: "(555)555-555", key: 1},
                                   {discription: "Info@website.com"},
                                   ];
                                      
           
    return( <div class="container-fluid"> 
            <h3 className = "areaTitle">{areaOfServiceTitle}</h3>  
            <ul className = "areaOfService">{areaOfService.map((area)=>{
                      return(
                               <li className="areaList" key={area.key}>{area.discription}</li>)         

           })}</ul>
            <h3 className = "companyTitle">{companyName}</h3>  
            <ul className = "companyOfService">{CompanyOfService.map((company)=>{
                      return(
                               <li className="areaList" key={company.key}>{company.discription}</li>)         

           })}</ul>
                
                 <hr/>
                 <p className="copyRight">Copyright &copy; 2021</p>

    </div>
    );
}

function ContactMain() {
    return ( <div>
              <Contact/>
              <AddInfo/>
              <ContactInfo/>
    </div> );
}

export default ContactMain;