import React from 'react';
import about1 from '../../image/about1.jpg'
import about2 from '../../image/about2.jpeg';
//import shower from '../../image/shower.jpg';
import Card from 'react-bootstrap/Card'
import CardGroup from 'react-bootstrap/CardGroup';




/* diplay of all the header's, images and text to describe the services offered.  */

const Headline = () =>{
            
               return( <div class="container-fluid"> 
                        <span className = "aboutTitle">About Us </span> 
             </div>);

}

const AboutUsHeader = () =>{
   
    return( <div class="container-fluid"> 
    <Card class="border-0" className = "card">
    <Card.Img className = "proplumImage" variant="middle"  src={about1}/>
    <Card.Body>
      <Card.Title className = "title">Professional Plumbers</Card.Title>
      <Card.Text className = "ProfPlumberText" >
      It is a long established fact that a reader will be distracted by the readable content a page when looking at its layout The point of using Lorem Ipsum is that it has a more-or-less normal distribution of letters, as opposed to using Content here, content here making it look like readable English
      </Card.Text>
    </Card.Body>
  </Card> </div>);

}

const MoreAboutUs = () =>{
       
      return( <div class="container-fluid"> 

<CardGroup>
  <Card>
    <Card.Img className = "drain" variant="middle" src={about2} alt="drain" />
    <Card.Body>
      <Card.Title className ="plumbOption">Plumbing Options</Card.Title>
      <Card.Text className ="plumOptText">
            It is a long established fact that a reader will be distracted by the  readable content
           a page when looking at its layout  The point of using Lorem Ipsum is that it has a more-or-less normal
          distribution of letters, as opposed to using Content here, content here making it look like readable English
      </Card.Text>
    </Card.Body>
   
 </Card>
  <Card>
    <Card.Img className ="shower" variant="middle" src= {about2}  alt = "shower"/>
    <Card.Body>
      <Card.Title className="plumService">Plumbing Service</Card.Title>
      <Card.Text className = "plumServiceText">
         As is a long established fact that a reader will be distracted by the  readable content
          The point of using Lorem Ipsum is that it has a  more-or-less normal distribution of letters, as opposed to using Content here French. 
      </Card.Text>
    </Card.Body>
   
  </Card>

</CardGroup>



      </div>);             
        

}


function About () {
    return ( <div class="container-fluid"> 
        <Headline/>
        <AboutUsHeader/>
        <MoreAboutUs/>
      
    </div>);
}

export default About;