import React from 'react';
import companyLogo from'../../image/companyLogo.jpg'  ; // interchangeable images
import waterrepair from '../../image/water-repair.jpg'; // ^
import Container  from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import $ from 'jquery';




/*display of images and titles of company. */


//company name logo in a a row and column so the image can not stretch 
const LogoImge = () => {  // logo for the company 
    return ( <div class="container-fluid">
      <Container>
          <Row>
            <Col xs={15} md={15}>
              <img className = "companyLogo" src = {companyLogo}  alt = "CompanyLog"  rounded />
            </Col>
            </Row>
    </Container>
    
    </div>
    );
  }
  //company phone # 
  const PhoneNumber = () => {
    const phoneNmber = <p>(555)-555-5555</p>
    return (<div className="phone"><p>{phoneNmber}</p></div>);
  }
 
  //company center logo 
const CenterLogo = () =>{

  return (<div class="container-fluid"> <img className = "centerLogo"  src = {waterrepair} alt="water-repair"/></div>);
}


const Appointment =()=>{
  $(window).scroll(function(){         // Jquery used to make the appointment box disapear as user scrolls down and appear as user
    $(".app-container").css("opacity",0.9 - $(window).scrollTop() / 550);  // scrolls up 
  });
 

  const appText = "To Book Appointment "; 

return ( 
      <div class="container-fluid"> 
         <div className = "app-container"><div className = "text-app">{appText}</div></div>  
             
  </div>);
}
    // Onclick accation directs the user to contact information so they are able to make appointment 
function AppointmentButton () {

    function handleClick(){
      window.location.href = "#ContactUs";
    
    }

    $(window).scroll(function(){         // Jquery used to make the appointment box disapear as user scrolls down and appear as user
      $(".app-button").css("opacity", 1 - $(window).scrollTop() / 550);  // scrolls up 
    });
   
      
return (<div class="container-fluid">
    {/* on click the users is directed to the contact portion of the page to be able to email the user for assistance  */}
   <button className="app-button" type="button" onClick={handleClick}>Book Appointment Now</button>
   </div>);
    }
    
 
 
 const header = () => {
    return (<div>
            
                <LogoImge/>
                <PhoneNumber/>
                <CenterLogo />
                <Appointment />
             
                <AppointmentButton/> 
                         
             </div>);
 }

export default header;
